footer {
    display: grid;
    height: 10vh;
    background-color: $black;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
        ". footer-info footer-links";
    .to-top {
        display: none;
    }
    .footer-info { 
        grid-area: footer-info;
        color: $white;
        text-align: center;
        align-self: center;
    }
    .footer-links { 
        grid-area: footer-links; 
        @include center-child;
        gap: 20px;
        img {
            height: 50px;
        }
    }
    @media (max-width: 768px) {
        display: flex;
        flex-direction: column;
        position: relative;
        margin-top: 20px;
        padding: 20px;
        height: auto;
        gap: 20px;
        .to-top {
            display: block;
            position: absolute;
            height: 100px;
            top: 20%;
            left: 10px;
        }
        .footer-links { 
            gap: 30px;       
        }
    }
}