.sidebar {
    position: fixed;
    top: 50%;
    left: 25px;
    padding: 25px 15px;
    z-index: 10;
    width: 100px;
    height: 100px;
    a {
        color: $white;
        font-size: 1rem;
        padding-left: 10px;
    }
    .background-image-container {
        position: absolute;
        top: -70%;
        left: -90%;
        width: 300%;
        height: 300%;
        background-image: url('../../../public/img/blue-blob.webp');
        background-size: cover;
        background-position: center;
        z-index: -1;
    }
    @media (min-width: 1921px) {
        left: 250px;
        a {
            font-size: 1.4rem;
        }
        .background-image-container {
            top: -140%;
            left: -180%;
            width: 500%;
            height: 500%;
        }
    }
}
