#projects-container {
    min-height: 100vh;
    margin-top: 100px;
    position: relative;
    margin: auto;
    display: flex;
    flex-direction: column;

    h3, h4 {
        text-align: center;
        margin-bottom: 2%;
    }
    h3 {
        @media (max-width: 768px) {
            margin-bottom: 50px;
          }
        @media (min-width: 1921px){
            margin: auto auto 150px;
        }
    }
    .projects {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
        gap: 10px;
        width: 100%;
        max-width: 1200px;
        margin: auto;
        .cards-container {
            position: relative;
            h4 {
                font-size: 1rem;
                color: $black;
                text-decoration: underline 3px $color-4;
                @media (min-width: 1921px) {
                    font-size: 1.4rem;
                }
            }
            p {
                width: 80%;
                text-align: center;
            }
            .project-card {
                height: 380px;
                border: 1px solid $black;
                border-radius: 10px;
                width: 580px;
                @include center-child;
                flex-direction: column;
                gap: 10px;
                position: relative;
                background-color: $color-2;
                box-shadow: 0px 3px 5px gray;
                opacity: 1;
                transition: border-radius .2s ease-in-out, opacity .2s ease-in-out;
                cursor:url('../../../public/img/cursor/Plus\ Symbol.cur'), auto;
                img {
                    width: auto;
                    min-width: 80%;
                }
            }
            .project-card-back {
                height: 380px;
                border: 1px solid $black;
                border-radius: 40px;
                width: 580px;
                background-color: $color-1;
                box-shadow: 0px 2px 2px gray;
                @include center-child;
                flex-direction: column;
                gap: 20px;
                position: absolute;
                top: 0;
                cursor:url('../../../public/img/cursor/Minus\ symbol.cur'), auto;
                .btn-link {
                    color: $white;
                    background-color: $color-4;
                    border-radius: 20px;
                    padding: 10px;
                }
            }
            .no-opacity {
                opacity: 0;
                transition: opacity .2s, border-radius .2s;
                border-radius: 40px;
                pointer-events: none;
            }
        }
        @media (max-width: 1200px) {
            grid-template-columns: 1fr;
            .cards-container {
                justify-self: center;
            }
        }
        @media (max-width: 600px) {
            .cards-container {
                .project-card, .project-card-back {
                    width: 350px; 
                    img {
                        max-width: 100%;
                        height: auto;
                    }
                }
            }
        }
    }
    .project-line {
        position: absolute;
        z-index: -1;
        opacity: .4;
    }
}