.profil{
    min-height: 80vh;
    position: relative;
    h1, h2, h3 {
        text-align: center;
        margin-bottom: 2.5%;
        @media (min-width: 1921px) {
            margin-bottom: 4%;
        }
    }
    img {
        display: block;
        margin: 0 auto;
        border-radius: 50%;
        height: 250px;
        width: 250px;
        object-fit: cover;
        outline: $black 6px double;
    }
    .resume{
        margin: 2% auto;
        width: 50%;
        text-align: center;
        @media (min-width: 1921px) {
            font-size: 1.5rem;
        }
    }
    .skills-container {
        display: flex;
        justify-content: space-between;
        margin-top: 2%;
        li {
            @include flex-wrap;
            max-height: 300px;
            width: 33%;
            gap: 40px;
            .skills {
                display: flex;
                flex-direction: column;
                margin-bottom: -20px;
                img {
                    height: 50px;
                    width: 50px;
                }
            .skills-label {
                margin: 10px auto;
                width: auto;
                text-align: center;
            }
            }
        }
    }
    @media (max-width: 1000px) {
        .resume {
            width: 80%;
        }
        .skills-container {
            li {
                img {
                    height: 40px;
                    width: 40px;
                }
                .skills-label {
                    max-width: 40px;
                    font-size: .9rem;
                }
            }
        }
    }
    @media (max-width:600px) {
        .skills-container {
            flex-direction: column;
            align-items: center;
            li {
                width: 90%;
                margin-bottom: 40px;
            }
        }
    }
}

.profil-line {
    position: absolute;
    top: -200px;
    left: 150px;
    z-index: -1;
    @media (max-width: 1000px) {
        top: 0;
        left: 0;
    }
    @media (min-width: 1921px) {
        top: -300px;
    }
}