#form-container {
  @include center-child;
  flex-direction: column;
  min-height: 90vh;
  overflow: hidden;
  position: relative;
  z-index: 5;
  @media (max-width: 768px) {
    min-height: 50vh;
  }
}
h3 {
  margin-top: 50px;

}
form{
    display: grid;
    width: 40%;
    min-width: 300px;
    max-width: 600px;
    margin: 0 auto;
    margin-top: 50px;
    gap: 10px;
    padding: 5px;
    background-color: $color-2;
    border-radius: 50px;
    .input-container {
      display: flex;
      flex-direction: column;
      label {
        font-size: 1.2rem;
        text-align: center;
        width: 80%;
        margin: auto;
        background-position: center;
        background-size: 100%;
        @media screen and (max-width: 768px) {
          font-size: 1.1rem;
        }
      }
      input {
        height: 40px;
        font-size: 1.2rem;
        border-radius: 15px;
        border: none;
        padding: 0 10px;
        text-align: center;
        @media screen and (max-width: 768px) {
          height: 4vh;
          width: 100%;
        }
      }
      textarea {
        resize: vertical;
        min-height: 250px;
        max-height: 700px;
        font-size: 1rem;
        border-radius: 15px;
        border: none;
        padding: 5px 10px;
        @media screen and (max-width: 768px) {
          height: 1vh;
          width: 100%;
          min-height: 150px;
        }
      }
      .btn-submit {
        cursor: pointer;
        border-radius: 5px;
        margin-bottom: 50px;
        transition: 1s;
        background-color: $color-2;
        width: 60%;
        margin: 10px auto;
        @media screen and (max-width: 768px) {
          height: 5vh;
          width: 80%;
        }
        &:hover {
          border-radius: 20px;
          transition: 1s;
          letter-spacing: 5px;
          background-color: $color-4;
        }
      }     
      &:nth-child(1) {
        label {
         background-image: url('../../../public/img/up-burger.webp');         
        }
      }
      &:nth-child(2) {
        label {
          background-image: url('../../../public/img/mid-burger.webp');         
         }
      }
      &:nth-child(3) {
        label {
          background-image: url('../../../public/img/bottom-burger.webp');         
         }
      } 
    }
    @media (max-width: 1000px) {
      width: 98%;
    }
    .back-name, .back-email, .back-textarea, .back-btn {
      background-position: center;
      background-size: 100%;
      width: 50%;
      height: 250px;
      left: 25%;
      position: absolute;
      z-index: -1;
      @media (max-width: 1000px) {
        display: none;
      }
      @media (min-width: 1921px) {
        width: 40%;
        height: 650px;
        left: 30%;
      }
    }
    .back-name {
      top: 12%;
      background-image: url('../../../public/img/splat/splat\ 3.webp');
      @media (min-width: 1921px) {
        top: 5%;
      }
    }
    .back-email {
      top: 20%;
      background-image: url('../../../public/img/splat/splat\ 2.webp');
      @media (min-width: 1921px) {
        top: 10%;
      }
    }
    .back-textarea {
      width: 52%;
      top: 40%;
      height: 210px;
      background-image: url('../../../public/img/splat/Text\ splat.webp');
      @media (min-width: 1921px) {
        width: 40%;
        left: 31.5%;
        top: 43%;
        height: 250px;
      }
    }
    .back-btn {
      top: 68%;
      height: 100px;
      background-image: url('../../../public/img/splat/splat\ 1.webp');
      @media (min-width: 1921px) {
        top: 63%;
      }
    }
  }
  .about {
    background-color: $color-4;
    border-radius: 50px;
    border: 1px solid $black;
    margin-top: 80px;
    height: 80px;
    width: 350px;
    @include center-child;
    p {
      font-size: 1.5rem;
    }
  }

  .form-message {
    position: absolute;
    top: 82%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    @media (min-width: 1921px) {
      top: 90%;
    }
  }
  .success, .error {
    width: 500px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
  }
  .success {
    background-image: url('../../../public/img/splat/message-success.webp');
    background-size: cover;
    background-position: center;
    font-size: 1.5rem;
    height: 80px;
    @media (min-width:1921px) {
      font-size: 1.6rem;
    }
  }
  .error {
    background-image: url('../../../public/img/splat/message-error.webp');
    background-size: cover;
    background-position: center;
    font-size: 1rem;
    height: 100px;
    @media (min-width:1921px) {
      font-size: 1.6rem;
      width: 750px;
      height: 200px;
    }
  }