.smart-nav {
    position: relative;
    height: 100px;
    margin-left: 20px;
    img {
        position: absolute;
        height: 80px;
        transition: 1s;
        z-index: 50;
        &:nth-child(1) {
            top: 0;
        }
        &:nth-child(2) {
            top: 20px;
        }
        &:nth-child(3) {
            top: 40px;
        }       
    }
}
.rotate-down {
    transform: rotate(45deg) translateY(35px);
    transition: 1s;
}
.hide {
    opacity: 0;
    transform: translate(-200px);
    transition: opacity 0.6s;
}
.open-nav.open {
    opacity: 1;
    pointer-events: auto;
}
.rotate-up {
    transform: rotate(-45deg) translateY(-35px);
    transition: 1s;
}

.open-nav {
    position: fixed;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $black;
    width: 80%;
    text-align: center;
    border: 1px solid $black;
    border-radius: 10px;
    top: 250px;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.6s;
    z-index: 50;
    a {
      text-decoration: none;
      color: $white;
      display: block;
      height: 100%;
    }
    li {
      font-size: 1.4rem;
      line-height: 100px;
    //   border-bottom: 1px solid $white;
      background-size: cover;
      background-position: center;
      &:hover {
        background-color: $color-1;
        color: $black;
      }
      &:nth-child(1) {
        background-image: url('../../../public/img/splat/splat-top.webp');
      }
      &:nth-child(2) {
        background-image: url('../../../public/img/splat/splat-middle.webp');
      }
      &:nth-child(3) {
        border-bottom: none;
        background-image: url('../../../public/img/splat/splat-bottom.webp');
    } 
    }
}
.overlay {
    position:fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $black;
    z-index: 40;
    opacity: 0.7;
    transition: 1s;
}