@font-face {
    font-family: "font-1";
    src: url(../fonts/Roboto-Regular.ttf);
}
@font-face {
    font-family: "font-2";
    src: url(../fonts/Roboto-Light.ttf);
}
@font-face {
    font-family: "font-3";
    src: url(../fonts/Roboto-Bold.ttf);
}

$font-1: "font-1", sans-serif;
$font-2: "font-2", sans-serif;
$font-3: "font-3", sans-serif;

$color-1: #dee2e5;
$color-2: rgba(152, 192, 212, 0.7);
$color-3: #546fe4;
$color-4: #50a5de;
$black: #04081d;
$white: rgb(253, 245, 245);

html {
    scroll-behavior: smooth;
}  
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: $font-1;
    text-decoration: none;
}
body {
    background: $white;
    color: $black;
    user-select: none;
    overflow-x: hidden;
}
li {
    list-style: none;
}
h3 {
    font-size: 1.3rem;
    @media (min-width: 1921px) {
        font-size: 1.5rem;
    }
}
h1 {
    @media (min-width: 1921px) {
        font-size: 2.2rem;
    }
}
h2 {
    @media (min-width: 1921px) {
        font-size: 1.6rem;
    }
}
h5 {
    @media (min-width: 1921px) {
        font-size: 1.2rem;
    }
}


@mixin flex-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
@mixin center-child {
    display: flex;
    justify-content: center;
    align-items: center;
}
.d-none {
    display: none;
}