nav {
    @include center-child;
    height: 100px;
    margin-bottom: 100px;
    ul {
        width: 50%;
        display: flex;
        justify-content: space-evenly;
        li {
            font-size: 1.5rem;
            @media (min-width: 1921px) {
                font-size: 1.8rem;
                margin-top: 50px;
            }
            a {
                color: $black;
            }
        }
    }
    svg {
        position: absolute;
        top: 0;
        z-index: -10;
    }
    @media (max-width: 1000px){
        height: 50px;
        margin-bottom: 50px;
        ul {
            justify-content: space-between;
            li {
                font-size: 1.2rem;
            }
        }
    }
}
